}

.button-hands:before {
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("victory-hand.c1160df2.svg");
  background-size: contain;
  margin-right: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.button-hands:after {
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("heart-hands.8dcbd299.svg");
  background-size: contain;
  margin-right: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 9px;
  transform: translateY(calc(35px - 40%));
}

button:hover:before {
  transform: translateY(-170%);
}

button:hover:after {
  transform: translateY(-50%);
}

